
.add-task-form {
    margin: auto;
    width: 80%;
    height: 320px;
    background-color: #3A3845;
    border-bottom-right-radius: 16px;
    border-bottom-left-radius: 16px;
    padding: 10px;
}
.form-control {
    width: 80%;
    height: 90px;
    margin: auto;
}
.task-label {
    font-size: 1.2rem;
    font-weight: bold;
    color: white;
    display: flex;
}
.task-input {
    width: 100%;
    height: 40px;
    display: flex;
    
}
.task-input-checkbox {
    width: 20px;
    height: 20px;
    
    
    
}
.task-label-reminder {
    font-size: 1.2rem;
    font-weight: bold;
    color: white;
    display: -webkit-inline-flex;
    
    
}
.save-task-btn {
    width: 40%;
    height: 40px;
    font-size: 1rem;
    background-color: black;
    border: solid 1px bisque;
    color: bisque;
    border-radius: 8px;
}
.save-task-btn:hover {
    width: 40%;
    height: 40px;
    font-size: 1rem;
    background-color: 1B1A17;
    border: solid 1px white;
    color: white;
    border-radius: 8px;
}