
.task-component {
    width: 80%;
    height: 100px;
    background-color: bisque;
    margin: auto;
    border-radius: 8px;
    
}
.task-h3 {
    display: flex;
    justify-content: space-between;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 4px;
}
.task-p {
    display: flex;
    padding-top: 0;
    padding-left: 10px;
    margin-top: 0;
    
}
.task-date {
    display: flex;
    justify-content: flex-end;
    padding-right: 10px;
    padding-bottom: 10px;
}
.delete-icon {
    color: red;
    cursor: pointer;
    padding: 5px;
    border-radius: 50px;
    border: solid 1px;
}
.delete-icon:hover {
    color: white;
    cursor: pointer;
    background-color: #FF8080;
    padding: 5px;
    border-radius: 50px;
    border: solid 1px;
}
.task-reminder {
    border-left: 5px solid green;
}